html {
    box-sizing: border-box;
    font-size: 62.5%;
    scroll-behavior: smooth;
    // background-color: $dark;
}

*,
*::after,
*::before {
    box-sizing: inherit;
    padding: 0;
    margin: 0;
}

body {
    font-family: $bodyFontSize;
    background-color: $blueDarkMain;
    color: $light;
    font-family: $bodyFont;
    font-weight: $lightW;
    color: $light;
    overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: hsl(176, 68%, 64%) transparent;


    &::-webkit-scrollbar {
        width: 6px
    }

    &::-webkit-scrollbar-track {
        background: transparent
    }

    &::-webkit-scrollbar-thumb {
        background-color: hsl(176, 68%, 64%);
        -webkit-transition: background-color .2s ease-in-out;
        transition: background-color .2s ease-in-out;
        &::-webkit-scrollbar-thumb:hover {
            background-color:hsl(176, 68%, 60%);
        }
    }


    &.no-scroll {
        overflow: hidden;
    }

    @include media('>=laptop') {}
}

h1,
h2,
h3,
h4 {
    font-family: $titleFont;
    font-weight: $boldW;
    color: white;
    line-height: 1.4;

}

p {
    font-family: $bodyFont;
    font-weight: $lightW;
    line-height: 1.7;
}

ul {
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}

li {
    list-style: none;
}

a {
    font-weight: $lightW;
    text-decoration: none;
    color: $light;
    transition: all .23s ease-in-out;
    cursor: pointer;

    &:hover {
        color: $cyanAccent;
    }
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
}